<template>
  <div class='title'>{{$t('Solution.Redirecting')}}</div>
</template>

<script>
export default {
  name: 'redirect-page',
  methods: {
    redirect () {
      if (this.$route.params.id != 0) {
        this.$router.push({
          name: this.$route.params.type,
          params: {
            id: this.$route.params.id
          }
        })
      }
      else {
        this.$router.push({
          name: this.$route.params.type,
        })
      }
    }
  },
  mounted () {
    setTimeout(() => {
      this.redirect();
    }, 100)
  }
};
</script>

<style scoped>
.title {
  margin-top: 100px;
  text-align: center;
}
</style>